<template>
  <div>
    <Width100>
      <Paragraph
        title="What is IGN?"
        paragraph="IGN is a video game and entertainment media platform where users can join the gamer community to connect with gamers and follow their favorite games. IGN Esports allows users  to stay up to date with the latest esports news and events."
        v-bind:center="true"
      />
    </Width100>
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import Paragraph from '../common/Paragraph';
import LineBreak from '../common/LineBreak';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
    components: {
      Carousel,
      LineBreak,
      Paragraph,
      VueImage,
      Width50,
      Width100,
    },

    data() {
      return {};
    },

};
</script>

<style lang="scss" scoped>

</style>



