<template>
  <div>
    <Width100>
      <Paragraph
        subtitle="New Design"
        v-bind:center="true"
      />
    </Width100>
    <Width100>
      <Paragraph
        paragraph="Wireframe: Redesigned to improve visual hierarchy and make it easier to navigate  the website by utilizing size, spacing, and weight."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
        <VueImage :src="ign_new_design_wireframe" alt="IGN Desktop" />
    </Width100>

    <LineBreak />
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import Paragraph from '../common/Paragraph';
import LineBreak from '../common/LineBreak';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import ign_new_design_wireframe from "../../assets/img/ign_new_design_wireframe.jpeg";

export default {
    components: {
      Carousel,
      LineBreak,
      Paragraph,
      VueImage,
      Width50,
      Width100,
    },

    data() {
      return {
        ign_new_design_wireframe,
      };
    },

};
</script>

<style lang="scss" scoped>
.img-container {
  padding: 0 32px;
  max-width: 720px;
  margin: 32px auto;
}
</style>



